

(function () {
    const alertaExitoFormulario = document.querySelector('.alerta__exito');
    const formulario = document.querySelector('.formulario');
    if (alertaExitoFormulario) {
        formulario.style.display = 'none';
        setTimeout(() => {
            alertaExitoFormulario.remove();
            redireccionar();

        }, 3000);

        function redireccionar() {
            const urlActual = window.location.href;
            if (urlActual.includes('/admin/usuarios/')) {
                window.location.href = '/admin/usuarios';
            }
            if (urlActual.includes('/admin/estratos/')) {
                window.location.href = '/admin/estratos';
            }
            if (urlActual.includes('/admin/registrados/')) {
                window.location.href = '/admin/registrados';
            }
            if (urlActual.includes('/admin/cajas/')) {
                window.location.href = '/admin/cajas';
            }
            if (urlActual.includes('/admin/contabilidad/terceros/')) {
                window.location.href = '/admin/contabilidad/terceros';
            }
            if (urlActual.includes('/admin/contabilidad/cuentas/')) {
                window.location.href = '/admin/contabilidad/cuentas';
            }
            if (urlActual.includes('/admin/contabilidad/bancos/')) {
                window.location.href = '/admin/contabilidad/bancos';
            }
            if (urlActual.includes('/admin/contabilidad/cuentas-bancarias/')) {
                window.location.href = '/admin/contabilidad/cuentas-bancarias';
            }
            if (urlActual.includes('/admin/contabilidad/egresos/')) {
                window.location.href = '/admin/contabilidad/egresos';
            }
            if (urlActual.includes('/admin/contabilidad/egresos-simples/')) {
                window.location.href = '/admin/contabilidad/egresos-simples';
            }
            if (urlActual.includes('/admin/contabilidad/consignaciones/')) {
                window.location.href = '/admin/contabilidad/consignaciones';
            }
        }
    }

})();




